.sidebar__row {
  display: flex;
  align-items: center;
  padding: 10px;
  cursor: pointer;
}
.sidebar__row:hover {
  background-color: rgb(231, 231, 231);
  border-radius: 10px;
}
.sidebar__row > h4 {
  margin: 0 0 0 20px;
  font-weight: 600;
}
.sidebar__row > .MuiSvgIcon-root {
  font-size: x-large;
  color: #2e81f4;
}
